.addNewSection{
    padding: 20px 30px;
    background: #f2f2f2;
    text-align: center;
    cursor: pointer;
    svg{
        width: 30px;
    }
}
.each_section_Wrp{
    text-align: center;
    border: 1px solid #eee;
    padding: 20px;
    cursor: pointer;
    border-radius: 20px;
    -webkit-transition: all ease-in-out 300ms;
    transition: all ease-in-out 300ms;
    .aicon{
        width: 80px;
        height: 80px;
        // background: #f2f2f2;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        svg{
            width: 40px;
            height: 40px;
            color: #c3c3c3;
        }

    }
    .section_title{
        font-size: 18px;
        font-weight: 500;
    }
   
}

.each_section_Wrp:hover, .each_section_Wrp.active{
    transform: scale(1.1);
    .aicon{
        svg{
            color: #fff;
        }
    }
    .section_title{
        color: #fff; 
    }
    background: #9B4DFF;    
}
.allSectionTypeWrp{
    border: 1px solid #eee;
    padding: 30px;
    position: relative;
}
.removeBtn {
    position: absolute;
    top: 10px;
    right: 10px;
    svg{
        width: 30px;
    }
}

.each_sections{
    position: relative;
    border: 1px solid #dddddd;
    margin-bottom: 20px;
    .each_section_header{
        background: var(--cui-primary);
        .form-control{
            background: transparent;
            border: none;
            border-radius: 0;
            color: #fff;
            &:focus{
                outline: none;
            }
        }
    }
    .each_section_body{
        padding: 30px;
    }
}